import React, {useState} from "react"
import ImageGrid from "./ImageGrid"
import Modal from "./Modal"
import useStorage from './Firebase/UseStorage';
import ImgsViewer from "react-images-viewer";
import Viewer from 'react-viewer';
import useWindowSize from "./Hooks/useWindowSize";

function Overview()
{
    const imgs=useStorage("images/");
    let windowSize = useWindowSize();
    const [selectedImg, setSelectedImg] = useState(-1);
    function onPrev()
    {
        if(selectedImg===0)
        {
            setSelectedImg(imgs.length -1);
        }
        else
        {
            setSelectedImg( (oldVal) => (oldVal-1))
        }
    }
    function onNext()
    {
        if(selectedImg===(imgs.length-1))
        {
            setSelectedImg(0);
        }
        else
        {
            setSelectedImg( (oldVal) => (oldVal+1))
        }
    }
    function close()
    {
        setSelectedImg(-1);
    }
    return (
        <div className="grid">
            <div className="title">
                <h2>Projects</h2>
                <p>Some stuff I have worked on</p>
            </div>
            <ImageGrid setSelectedImg={setSelectedImg} imgs={imgs} />
        <Viewer className="viewer" images={imgs} activeIndex={selectedImg}
        visible={selectedImg!=-1}
            onClose={close}
            noToolbar={true}
            zoomable={false}
            rotatable={false}
            drag={false}
            onMaskClick={close}
        />
        </div>
    )
}

export default Overview