import React, { useState } from 'react';
import {
  MDBContainer,
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarToggler,
  MDBNavbarNav,
  MDBNavbarItem,
  MDBNavbarLink,
  MDBCollapse,
  MDBIcon
} from 'mdb-react-ui-kit';
import {Link} from "react-router-dom"

export default function NavigationBar() {
  const [showNav, setShowNav] = useState(false);

  return (
    <MDBNavbar expand='lg' light bgColor='light'>
      <MDBContainer fluid>
        <MDBNavbarBrand href='#'>
          <p fontFamily="Homemade Apple" className="aeena">The Aeena</p>
        </MDBNavbarBrand>
        <MDBNavbarToggler
          type='button'
          aria-expanded='false'
          aria-label='Toggle navigation'
          onClick={() => setShowNav(!showNav)}
        >
          <MDBIcon icon='bars' fas />
        </MDBNavbarToggler>
        <MDBCollapse navbar show={showNav}>
          <MDBNavbarNav>
            <MDBNavbarItem>
              <Link to="/">
                <MDBNavbarBrand>Home</MDBNavbarBrand>
              </Link>
            </MDBNavbarItem>
            <MDBNavbarItem>
            <Link to="/overview">
                <MDBNavbarBrand>Overview</MDBNavbarBrand>
              </Link>
            </MDBNavbarItem>
            <MDBNavbarItem>
              <Link to="/about">
                <MDBNavbarBrand>About</MDBNavbarBrand>
              </Link>
            </MDBNavbarItem>
          </MDBNavbarNav>
        </MDBCollapse>
      </MDBContainer>
    </MDBNavbar>
  );
}